* {
  font-family:  sans-serif;
}
body{
  padding: 60px;
  color:white;
  background-color: #1a1c20;
}

h1{
  font-size: 3rem;
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%;
  height: 35px;
  border-radius: 5px;

}

.card{
  align-content: center;
  border: 1px solid grey;

  border-radius:  6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;


}
.card p {
  text-align: center;
  padding: 5px;
}
.card img{
  max-height: 170px;
}
.card-grid{
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}